<template>
  <div class="searchList">
    <van-nav-bar
    title="搜索结果"
    left-text="主页"
    left-arrow
    @click-left="$router.push('/home')"
    />
     <van-search
      :show-action="false"
      placeholder="请输入搜索关键词"
      v-model="searchValue"
      shape="round"
      input-align="center"
      @click="onSearch"
    >
    </van-search>
    <van-dropdown-menu>
      <van-dropdown-item v-model="value1" :options="option1" />
      <van-dropdown-item v-model="value2" :options="option2" @change="handleChange" ref="sortGroup"/>
      <van-dropdown-item v-model="value3" :options="option3" />
    </van-dropdown-menu>
    <van-empty image="search" description="空空如也" v-if="resultList.length==0" />
    <div class="resultList" v-else>
      <van-card v-for="item in resultList" :key="item.goods_id"
      :num="item.goods_sales"
      :price="item.goods_price_max"
      :title="item.goods_name"
      :thumb="item.goods_image"
      :origin-price="item.line_price_max"
      @click="handleClick(item.goods_id)"
      />
    </div>
  </div>
</template>

<script>
import { queryGoods, queryGoodsSort } from '@/api/searchGoods'
export default {
  name: 'SearchListIndex',
  data () {
    return {
      searchValue: this.$route.query.search,
      resultList: [],
      value1: 0,
      value2: 0,
      value3: 30,
      option1: [
        { text: '综合', value: 0 },
        { text: '好评率', value: 1 },
        { text: '评论数', value: 2 }
      ],
      option2: [
        { text: '销量', value: 0 },
        { text: '价格', value: 1 }
      ],
      option3: [
        { text: '配送', value: 30 },
        { text: '包邮', value: 31 },
        { text: '极速发货', value: 32 }
      ]
    }
  },
  created () {
    this.getSearchResult()
  },
  methods: {
    onSearch () {
      this.$router.push('/search')
    },
    async getSearchResult () {
      const res = await queryGoods(this.searchValue)
      // console.log('res', res)
      this.resultList = res.data.list.data
      // console.log('result', this.resultList)
    },
    async handleChange () {
      const res = await queryGoodsSort(this.searchValue, this.$refs.sortGroup.value)
      // console.log('res', res)
      this.resultList = res.data.list.data
      // console.log('result', this.resultList)
    },
    handleClick (goodsId) {
      // console.log(goodsId)
      this.$router.push(`/product?goodsId=${goodsId}`)
      // console.log('click abd', goodsId)
    }
  }
}

</script>

